.auth__main{
    align-items: center;
    background-color: $primary;
    display: flex;
    justify-content: center;
    margin: 0px;
    height: 100vh;
    width: 100vw;
};

.auth__box-container{
    background-color:white;
    box-shadow: 0px 3px 7px $dark-grey;
    border-radius: 2px;
    padding: 20px;
    width: 250px;
};

.auth__title{
    color: darken($color: $primary, $amount: 10);
    margin-bottom: 20px;
};

.auth__input{
    color: $dark-grey;
    border: none;
    border-bottom: 1px solid $ligth-grey;
    font-size: 16px;
    height: 20px;
    width: 100%;
    margin-bottom: 10px;
    transition: border-bottom 0.3s ease;

    &:focus{
        border-bottom: 1px solid $primary;
        outline: none;
    }
};

.auth__social-networks{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;    
};

.auth__alert-error {
    background-color: red;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px
}
